html {
  font-size: 18px;
}

html * {
  box-sizing: border-box;
}

body {
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 1rem;
  color: white;
  background: #121214;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.card {
  padding: 1rem;
  line-height: 1.66em;
}

.body_content {
  width: 100%;
  padding: 1rem;
  margin: 1rem auto;
  text-align: center;
}
.loading-spinner-element {
  -webkit-animation: loadingSpinnerRotate linear 600ms;
          animation: loadingSpinnerRotate linear 600ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}

@-webkit-keyframes loadingSpinnerRotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes loadingSpinnerRotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.loading_container {
  width: 100%;
  height: 100%;
  background: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

@-webkit-keyframes pulse {

  0%,
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
  }
}

@keyframes pulse {

  0%,
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
  }
}

.column_container {
  max-width: 80vw;
  margin: 1rem auto;
}

.column {
  background: #626264;
  border-radius: 0.25em;
  margin: 1rem 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  box-shadow: 0.125em 0.125em 0.25em rgba(0, 0, 0, 0.15);
  transition: cubic-bezier(0.27, 0.05, 0.45, 1.29) 450ms;
}

.column footer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: auto;
}

.column .button {
  text-decoration: none;
  background: linear-gradient(25deg, purple, orangered, hotpink);
  color: white;
  text-align: center;
  padding: 0.5em 1em;
  border-radius: 0.25rem;
  transition: cubic-bezier(0.27, 0.05, 0.45, 1.29) 300ms;
  margin-top: 0.5rem;
  flex-grow: 1;
}

.column .button:first-of-type {
  margin-right: 0.25rem;
}

.column .button:last-of-type {
  margin-left: 0.25rem;
}

.column .button svg {
  display: inline-block;
  vertical-align: middle;
  color: currentColor;
}

.column .button:hover svg {
  color: white;
  -webkit-animation: pulse 1 450ms;
          animation: pulse 1 450ms;
}

@media screen and (min-width: 48rem) {
  .column_container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: stretch;
    justify-content: space-evenly;
  }

  .column_container:hover .column {
    opacity: 0.5;
  }

  .column {
    width: calc(33.33% - 2rem);
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  .column_container:hover .column:hover {
    opacity: 1;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

  .column .button {
    flex-grow: 0;
  }
}

.fancy-select {
  display: inline-block;
  padding: 0;
  position: relative;
  z-index: 0;
}

.fancy-select:after {
  content: "\25BC";
  font-size: inherit;
  color: rgba(255, 255, 255, 0.33);
  position: absolute;
  top: 50%;
  right: 0.5em;
  z-index: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.fancy-select select {
  -webkit-appearance: none;
  background: none;
  outline: none;
  border: 0;
  width: 10rem;
  padding-top: 0.5em;
  padding-right: 1em;
  padding-bottom: 0.5em;
  padding-left: 1em;
  border: 0.0625rem solid rgba(255, 255, 255, 0.66);
  color: white;
  font-size: 1rem;
}
.hero {
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  background-position: center center;
  padding: 0;
  margin: 0;
}

.hero figcaption {
  color: white;
  font-size: 2vmax;
  text-align: center;
  padding: 5vmax 1rem;
}

.hero figcaption h1 {
  margin: 0;
  line-height: 1em;
  letter-spacing: 0.0625vmin;
}

.hero figcaption h1 span {
  background: linear-gradient(25deg, purple, orangered, hotpink);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
